/*=== Slideshow ===*/
.lazy-loading-img #home-slideshow .swiper-slide.lazyload {
    background-image: var(--bg-loading) !important;     
    width: 100% !important;
    background-size: 100px auto !important;
    @include opacity(1 !important);
    .swiper-content {
        @include opacity(0);
    }
}
#home-left-column + #home-main-content { 
    .bc-wrapper.boxed { 
        @include padding-start(0);
        @include padding-end(0);
    }
} 
.bc-wrapper {
    position: relative;
    overflow: hidden;
    &.full-padding {
        @include padding-start(5%);
        @include padding-end(5%);
    } 
    &.full-height .swiper-slide {
        min-height: 100vh;
        img {
            &.img-desk, &.img-mobile {
                min-height: 100vh;
            }
        }
    }
}
.slideshow-catalog-wrapper {
    @include translate3d(0, 0, 0);
    &.sidebar-banner {
        .bc-home-slideshow {
            @media (min-width: 1200px) {
                width: 75.714286%; 
                float: left;
            }
        }
        .tab-wrapper {
            display: none;
        } 
        .bc-three-banner {
            @media (min-width: 1200px) {
                width: 24.2857145%;
                float: left;
                @include padding-start(20px);  
            } 
            @media (max-width: 1199px) {
                padding-top: 20px;
                overflow: hidden;
                @include flexbox;  
                @include margin-start(-10px);
                @include margin-end(-10px);
            } 
            @media (max-width: 767px) {
                display: block;
            }
            &.style-1 {
                .bc-item:first-child  .bc-item-inner {
                    @media (min-width: 1300px) {
                        border-top: none; 
                    }
                } 
                .bc-item-inner { 
                    height: 178px;
                    flex-wrap: wrap;  
                    border-top: 1px solid var(--body-border-color);
                    .bc-item-left {
                        @include ab(n, n, -30px, -10px);
                        width: 110px; 
                        height: auto;
                        @include transform(rotate(-16deg)); 
                    }
                }
            }
            &.style-2 .bc-item {
                margin-bottom: 15px;
                @media (max-width: 1199px) {
                    padding: 0 10px; 
                    margin-bottom: 0;    
                } 
                @media (max-width: 767px) {
                    margin-bottom: 15px;
                }
                &:last-child {
                    margin-bottom: 0; 
                }
                .bc-item-left {
                    width: 50%;
                    @include margin-end(4%);
                }
                .bc-item-body {
                    width: 50%; 
                }
            } 
            .bc-item {
                flex-grow: 1; 
            }
            .bc-item-inner {
                position: relative;
                overflow: hidden; 
                height: 100%; 
                @include flexbox;
                align-items: center;
                padding: 14px 5px;  
                @include padding-end(20px);
                .bc-item-body {
                    margin: auto;
                    .bc-item-text {
                        font-weight: 200;
                        text-transform: uppercase;
                        font-size: calc(var(--body-font-size) + 3px);
                        a {
                            color: var(--body-general-color); 
                        }
                    }
                    > a {
                        font-size: var(--body-font-size);
                        line-height: 30px;
                        margin-top: 0;
                        position: relative;
                        font-weight: var(--font-bold); 
                        color: var(--body-general-color);
                        &:after {
                            content: url("data:image/svg+xml,%3Csvg version='1.1' xmlns='http://www.w3.org/2000/svg' width='14' height='20' viewBox='0 0 22 64'%3E%3Cpath d='M6.272 14.336l14.464 15.104q1.024 1.024 1.024 2.56 0 1.408-1.024 2.432l-14.464 15.104q-1.024 1.024-2.56 1.024t-2.56-1.024q-2.304-2.304 0-5.12l11.904-12.416-11.904-12.544q-2.304-2.816 0-5.12 1.024-1.024 2.624-1.024t2.496 1.024z' fill='#ffffff'%3E%3C/path%3E%3C/svg%3E");
                            width: 20px;
                            height: 20px;
                            text-align: center;
                            @include ab(0, n, n, -30px);
                            background-color: var(--main-color);
                            @include border-radius-circle; 
                        }
                        .upto {
                            text-transform: uppercase;
                            font-weight: 300;
                            display: inline-block;
                            .prefix {
                                display: inline-block;
                                width: 19px;
                                margin-bottom: 2px;
                                line-height: 1;
                                word-break: break-word;
                                @include margin-end(4px);
                            }
                            .value {
                                font-weight: 700;
                                font-size: 30px;
                            }
                            .suffix { 
                                position: relative;
                                font-weight: 700;
                                vertical-align: top; 
                                top: -3px;
                                @include margin-start(2px);
                            }
                        }
                    }
                }
            }  
            > a {
                display: block;
                text-align: center;
                margin: 0 0 12px;
                @media (max-width: 1024px) {
                    margin: 0;
                    width: 33.33333%;
                    padding: 0 10px;
                    float: left;
                } 
                &:last-child {
                    margin: 0;
                } 
                img {
                    width: 100%; 
                }
            } 
        } 
    }
} 
.bc-home-slideshow {  
    position: relative; 
    .swiper-container {
        height: 100%;
        &:before {
            content: "";
            @include ab(0, 0, n, n);
            width: 100%;
            height: 100%;
            background: #f5f5f5; 
        }
        &.swiper-container-initialized:before {
            content: none;
        } 
    } 
    .swiper-slide {
        position: relative;  
        height: auto;
        margin: 0 auto;   
        visibility: hidden;
        @include opacity(0);
        .img-mobile { 
            @media (min-width: 768px) {
                display: none;   
            }     
        }
        .img-desk { 
            @media (max-width: 767px) {
                display: none;
            }
        }
        &.has-video {
            .container-box {
                @include opacity(0);
            }     
            .video {
                min-width: 100%;
                min-height: 100%;    
                @include ab(50%, 50%, n, n);
                @include translate(-50%, -50%); 
            }     
            iframe {
                max-width: 100%;
                width: 100%;
                height: 100%;
            }
            &:hover .container-box {
                @include opacity(1);
            }
        }
        &.swiper-slide-active { 
            visibility: visible; 
            @include opacity(1);   
        }   
    }
    .swiper-content {
        @include ab(0, 0, 0, 0); 
        .container-box {
            position: absolute;
            z-index: 99;
        } 
    }
    &:hover .swiper-button-white {
        @media (min-width: 1025px) {
            @include opacity(1); 
        } 
    }  
    /*=== tab-wrapper ===*/
    .tab-wrapper {
        max-width: 1200px;
        padding: 0 15px;
        margin-top: 15px;
        display: none; 
        @media (min-width: 1200px) {        
            @include ab(n, 0, 0, 0);  
            display: block;      
            margin: 0 auto;
        }    
        .tab-content {
            position: relative;
            clear: both;
            z-index: 2;
            .tab-pane {
                @include ab(0, 0, n, n);
                display: block;
                @include opacity(0); 
                width: 100%;
                &.active {
                    position: static;
                    @include opacity(1);
                }
            }
            .slideshow-tabs-list {
                margin-bottom: 50px;  
            }
            .owl-stage-outer {
                margin-bottom: 0;
                padding-bottom: 0;
            }
            .owl-stage {            
                padding-top: 2px;   
                .owl-item .product-wrapper {
                    width: 100%;
                    float: none;
                    @include margin-end(0); 
                }        
            }
            .product-wrapper {
                width: 158px;
                float: left;
                @include margin-end(10px);
                &:before {
                    content: none;
                }
                &:after {
                    bottom: 0;
                }
                .product-head .product-name {
                    line-height: 19px;
                    overflow: hidden;
                    display: -webkit-box;
                    display: -ms-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                    margin: 0 0 10px;
                } 
                .product-content .pc-inner .price-cart-wrapper {
                    border: none;
                    padding-bottom: 0;
                }
                &:hover .product-content .product-button {
                    display: none;
                } 
            }
            .owl-carousel {
                @include middle-button-carousel (25px, -38px, -38px); 
            }
        }
    }
    .nav-tab-wrapper {  
        z-index: 2;                     
        width: 200px;
        background: var(--body-background-color);
        float: right;
        margin: 0 0 3%; 
        @include border-radius(5px, 5px, 5px, 5px);
        .nav {          
            border: none;
            padding: 0;
            overflow: initial;  
            &:before {
                content: none;
            }  
        } 
        .nav-item {
            display: block;
            margin: 0;                  
            a {
                display: block;
                text-align: start;
                line-height: 22px;
                padding: 7px 25px;              
                font-size: var(--body-font-size);             
                &:before {
                    content: none;
                }
                &:after {
                    content: '';
                    width: 26px;
                    height: 26px;
                    background: transparent;
                    @include ab(5px, -13px, auto, auto);
                    @include rotate(45);
                    @include border-radius-reset;
                    @include transition(all 0.35s ease 0s);
                }
                &:hover {                   
                    font-weight: var(--font-bold);
                }
                &.active {
                    background: var(--main-color);
                    color: var(--main-color-text);
                    &:after {
                        background: var(--main-color);
                    }
                }
            }
            &:first-child a.active {
                @include border-radius(0, 5px, 0, 0);
            }       
            &:last-child a.active {
                @include border-radius(0, 0, 5px, 0);
            } 
        }   
    }
    .image-layer-box {
        @include ab(0%, n, n, n); 
        height: 100%;
        display: none;  
        @media (min-width: 768px) {
            display: block;
        } 
        .image-layer-slide {
            height: 100%;  
            position: relative;  
            -webkit-animation-duration: 1s;
            animation-duration: 1s;
            -webkit-animation-fill-mode: backwards;
            -moz-animation-fill-mode: backwards;
            -ms-animation-fill-mode: backwards;
            -o-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
            -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1; 
        }   
        .image-lazysize { 
            position: static !important;
            padding: 0 !important;
            height: 100%;
            img {
                position: static; 
                object-fit: contain;
                max-width: 600px;
                height: auto;
            }  
        }  
    } 
}
.slideshow-01 .fix-textbox .container-box { 
    @media (min-width: 1200px) {
        @include padding-start(calc(24.3% + 30px));
    }    
}
.fix-boxed .slideshow-02 .wrap-stb  {
    @media (min-width: 1440px) {
        max-width: 1200px;   
    }
} 
.slideshow-02 {
    &.sidebar-banner {
        .bc-wrapper {
            padding-bottom: 20px;
        }
        .bc-home-slideshow {
            width: 100%;
            float: none;
            @media (min-width: 1300px) {
                width: calc(100% - 250px);
                float: left;
            }
        }
        .bc-three-banner {
            padding: 0;
            margin: 0;
            overflow: hidden;
            width: 100%;
            @media (max-width: 767px) {
                clear: both;  
                float: none;
                display: block; 
            }
            @media (max-width: 1299px) {
                @include flexbox; 
            } 
            @media (min-width: 1300px) {
                width: 250px;
            }
            > a {
                margin: 0; 
                @media (max-width: 1299px) {
                    width: 33.33333%;
                    padding: 0;
                    float: left; 
                }  
                &:not(:last-child) {
                    @include border-end(1px solid var(--body-border-color));
                    @media (min-width: 1300px) {
                        @include border-end(none);
                        border-bottom: 1px solid var(--body-border-color);
                    }
                }
            }
        } 
    }
    .wrap-stb {
        @include width-boxed; 
        @include clearfix; 
        &.fix-textbox .bc-home-slideshow {
            @media (min-width: 992px) {
                @include padding-start(270px);
            } 
        } 
    }
    .block-collection { 
        vertical-align: top;
        letter-spacing: -0.31em;   
        background: var(--body-background-color);
        overflow: hidden;  
        > .collection-item {        
            vertical-align: top;
            letter-spacing: 0;
            @include border-end(1px solid var(--body-border-color)); 
            display: none; 
            @media (min-width: 480px) {
                width: 50%; 
            }
            @media (min-width: 768px) {
                width: 20%;
                display: inline-block; 
            }
            &:first-child {
                @include border-start(1px solid var(--body-border-color));     
            }
            .category {
                border-bottom: none;
                padding: 15px;                
                &:before {
                    height: 200%;
                    top: 0;
                    bottom: auto;
                }
                &:hover {
                    @media (min-width: 1025px) {
                        @include box-shadow(0 0 18px -2px rgba(0,0,0,.2));
                        &:before {
                            content: '';
                        }
                    }
                }
                .category-name {
                    font-size: var(--body-font-size); 
                    font-weight: var(--font-bold);
                }
                .category-image {
                    margin-bottom: 10px;
                    img {
                        width: auto;                        
                    }
                }
            }
        }
    }    
    .swiper-pagination {  
        bottom: 20px; 
        left: 38px;
        @include translate(0, 0); 
    } 
    &.sidebar-product {
        .bc-home-slideshow {
            width: 100%;
            float: none;  
            @media (min-width: 1200px) {
                width: calc(100% - 280px);   
                float: left; 
            }
        } 
        .bc-products {
            width: 280px;
            float: left;
            position: relative; 
            padding-bottom: 15px; 
            @include padding-start(20px);  
            @media (max-width: 1199px) {
                display: none;
            }
        }
        .bc-product-list {
            border: 2px solid var(--body-border-color);
            padding-bottom: 22px;
            position: relative;
            @include border-radius-10; 
            .product-wrapper {
                @include border-radius-10;
                &:not(:first-child) {
                    display: none;   
                }
                &:before {
                    content: none;
                }
                &:after {
                    bottom: -22px;   
                    @include border-radius-10; 
                }   
                .product-content {
                    .product-button {
                        display: none;  
                    }
                    .price-cart-wrapper {
                        border-bottom: none;   
                    }
                }
            }  
            .meta-countdown {
                @include ab(n, 30px, -38px, 30px); 
                z-index: 999; 
                .wrapper-countdown {
                    @include flexbox;
                    justify-content: center;
                    align-items: center;
                    font-size: var(--body-font-size);
                    font-weight: var(--font-bold); 
                    padding: 9px 15px;   
                    color: var(--body-general-color); 
                    background: var(--main-color);
                    @include border-radius-50;
                } 
                .countdown-section {
                    position: relative;
                    @include padding-start(8px);
                    &:before {
                        content: ":";
                        @include ab(50%, 3px, n, n);        
                        @include translate(0, -50%); 
                    }   
                    &:first-child {
                        &:before {
                            content: none;    
                        }
                    }
                    .countdown-amount {
                        background: none;
                        font-size: var(--body-font-size); 
                        color: inherit; 
                    }
                    .countdown-period {
                        display: none;
                    }
                }
            }
            &.owl-carousel {    
                @include middle-button-carousel(var(--body-font-size), 0, 0); 
                .owl-stage-outer {
                    padding-bottom: 40px;
                    margin-bottom: -40px;
                }    
            }    
        }     
    }   
} 
.container-box { 
    padding: 0;        
    position: relative;    
    > div {        
        background: transparent;  
        line-height: 1.0;
        padding: 0;     
        margin: 0 auto;   
        text-transform: uppercase;
        font-family: var(--font-heading);  
        position: relative;                                                         
        -webkit-animation-duration: 1s;
        animation-duration: 1s;
        -webkit-animation-fill-mode: backwards;
        -moz-animation-fill-mode: backwards;
        -ms-animation-fill-mode: backwards;
        -o-animation-fill-mode: backwards;
        animation-fill-mode: backwards;
        -webkit-animation-iteration-count: 1;
        animation-iteration-count: 1;              
        font-weight: 300; 
        b, strong {
            font-weight: var(--font-bold); 
        }
        &.heading {   
            margin: 0 auto 2.3%;
            padding: 0;                     
            line-height: 0.9;    
            &.bold-true {
                font-weight: 700;
            }           
        } 
        &.subheading {              
            line-height: 1.3;      
            margin: 0 auto 3%; 
            .big-font {
                font-weight: 700;
            }    
            &.bold-true {
                font-weight: 700;
            }           
        }
        &.cation {             
            margin: 0;                           
            line-height: 1.2;                       
            display: inline-block;     
            .big-font {
                font-weight: 700;
            }
            .title-price {
                display: block;
                text-transform: uppercase;
                font-size: var(--body-font-size);
                line-height: 1.3;
            } 
            > span {
                font-weight: 700;
                font-size: 170%;
                @media (min-width: 768px) {
                    font-size: 340%;  
                }
            }
            sup {
                font-size: 20px;
                top: -12px;
                left: 10px;
                @media (min-width: 768px) {
                    font-size: 30px;
                    top: -20px;
                }      
            }
            > label {
                font-size: 100%; 
                font-weight: 700;       
                margin: 5px 0 0 0;
                vertical-align: top;
                @media (min-width: 768px) {
                    margin-top: 15px;
                }
            }     
            p {
                margin: 0;
                font-weight: 700;
                font-size: 45px;
                line-height: 1.0;    
                @media (min-width: 992px) {  
                    font-size: 57px;  
                }     
                .small-size {
                    vertical-align: top;            
                    font-size: 28px; 
                    display: inline-block;
                    margin-top: -5px;   
                    @media (min-width: 992px) {
                        font-size: 38px;
                    } 
                } 
            } 
            &.bold-true {
                font-weight: 700;
            }                  
        }
        &.caption-link {
            margin: 1.5% 0 0;     
            position: relative;  
            .slider-button {                    
                font-weight: 400;
                font-size: var(--body-font-size);
                padding: 5px 25px;
                line-height: 26px;
                text-transform: none;
                display: inline-block;    
                @include border-radius-10;  
                @media (min-width: 768px) { 
                    font-size: calc(var(--body-font-size) + 2px);
                    padding: 9px 58px;
                }           
            }
        }
    }  
}
.swiper-button-white {  
    z-index:4; 
    display: block; 
    width: 30px;
    height: 30px;
    background: none;
    color: var(--main-color);
    border: none;
    text-align: center;         
    background: none;
    outline: none; 
    margin: 0;  
    @include ab(50%, n, n, n);
    @include translate(0, -50%);
    @include border-radius-circle;
    @include transition(all 0.35s ease 0s);  
    @media (min-width: 768px) {
        height: 60px; 
        width: 60px; 
        color: var(--white);
        background: var(--button-hover-btn1);
        background-image: none;  
    }        
    @media (min-width: 1025px) {
        @include opacity(0);
    }  
    svg path {
        fill: var(--white);
    }
    &.swiper-button-prev {
        background-image:  none; 
        left: 0; 
        @media (min-width: 768px) {
            left: 45px;
        }        
    }  
    &.swiper-button-next {
        background-image: none;   
        right: 0;    
        @media (min-width: 768px) {
            right: 45px;
        }  
    }  
}  
.swiper-pagination {
    bottom: 4%;
    left: 50%;
    z-index: 2;
    @include translate(-50%, 0);
    .swiper-pagination-bullet {
        width: 8px;
        height: 8px;
        margin: 0 7px;
        background: var(--bg-other);
        outline: none; 
        @include opacity(1);
        @include border-radius-circle;
        @include transition(all 0.3s ease 0s);
        &:hover,
        &.swiper-pagination-bullet-active {
            background: var(--main-color);
            width: 30px;
            @include border-radius(4px, 4px, 4px, 4px);
        }
    } 
}
.swiper-wrapper {  
    .owl-controls .owl-dots {
        @include ab(n, n, 6%, 0);
        margin: 0 auto;
        text-align: center;
        width: 100%;
        .owl-dot {
            margin: 0 5px !important;
            display: inline-block;
            line-height: 20px;
            span {          
                width: 12px !important;
                height: 12px !important; 
                margin: 0;     
                background: var(--main-color);
                border: 1px solid var(--main-color); 
                @include opacity(0.5);
                position: relative;
                display: inline-block;
                padding: 0;               
                @include border-radius-circle; 
            }
            &:hover,
            &:focus,
            &.active {
                span {  
                    @include opacity(1); 
                }
            }
        }
    }
}
#home-left-column {
    .left-margin-top {
        margin: 30px 0 0;
    }
    .sb-vertical-menu {
        margin-bottom: -15px;
    }
}
#home-main-content {
    &.col-lg-9 {
        .bc-home-slideshow .swiper-content {
            @media (min-width: 992px) {
                padding: 0 40px; 
            }
        }
        .btn-pagination .swiper-pagination {
            @include translate(0, 0);
            left: 15px;
            @media (min-width: 992px) {
                left: 40px;
            }
        }
        .swiper-button-white {
            width: 45px;
            height: 45px;
            &:before {
                line-height: 45px;
            }
            &.swiper-button-prev {
                left: 0;
            }
            &.swiper-button-next {
                right: 0;
            }
        }
        .nav-tab-wrapper .nav {
            &.align-center {
                @media (max-width: 1199px) and (min-width: 992px) {
                    position: static;
                    @include translate(0, 0);
                }
            }
            &.align-right {
                @media (max-width: 1199px) and (min-width: 992px) {
                    position: static;
                }
            }
        }
        #bottom-widget .container {
            padding: 0;
        }
