            line-height: 1.0;
            font-weight: 700;
        }
        &.h1, &.h2, &.h3, &.h4, &.h5, &.h6 {
            font-weight: 100;
        }
    } 
    .separator-sub-line {   
        max-width: 100%; 
        margin: 0 auto 2%;  
        overflow: hidden;   
        border: none;
        border-top-width: 1px;
        &.sub-line-bt {
            margin: 2% auto 0;        
        }
        &.sub-double-line {
            max-width: 80px;
            border-width: 1px;
        }
    }    
    &.side-line {
        h2 {
            position: relative;      
            margin: 0 auto;
            display: inline-block;
            padding: 0 30px;
            @media (min-width: 480px) {
                padding: 0 50px;
            }
            span {
                display: inline-block;
                padding: 0 20px;
                position: relative;
                z-index: 1;
                white-space: nowrap;
                background: var(--body-background-color);
            }
            &:after,
            &:before {
                content: '';
                height: 1px;
                width: 50%;  
                border: none; 
                border-top-width: 1px;
                @include ab(50%, n, n, n);
                @include translate(0, -50%);
            }
            &:before {
                left: 50%;
            }
            &:after {
                right: 50%;
            }
        }    
        &.line-solid h2 {
            &:after,
            &:before {
                border-style: solid;
            }
        }
        &.line-dotted h2 {
            &:after,
            &:before {
                border-style: dotted;
            }
        }  
        &.line-dashed h2 {
            &:after,
            &:before {
                border-style: dashed;
            }
        }  
        .separator-logo {
            margin: 8px auto 13px;
            display: block;
        }
        .separator-subtext {
            margin: 8px auto 0;            
        }
    } 
    &.left-line {
        text-align: start;
        h2 {
            position: relative;
            padding: 0;
            display: block; 
            &:after {
                content: none !important;
            }   
            &:before {
                content: '';
                height: 1px;
                width: 100%;
                border-width: 1px;
                @include ab(50%, n, n, 0);
                @include translate(0, -50%);
            }
            span {
                display: inline-block;
                position: relative;
                background: var(--body-background-color);
                padding: 0;
                @include padding-end(8%); 
            }       
        }
        &.line-solid h2:before {
            border-style: solid;
        }  
        &.line-dotted h2:before {
            border-style: dotted;
        } 
        &.line-dashed h2:before {
            border-style: dashed;
        }
    }
    &.right-line {
        text-align: end;
        h2 {
            position: relative;
            padding: 0;
            display: block; 
            &:after {
                content: none !important;
            }   
            &:before {
                content: '';
                height: 1px;
                width: 100%;
                border-width: 1px;
                @include ab(50%, 0, n, n);
                @include translate(0, -50%);
            }
            span {
                display: inline-block;
                position: relative;
                background: var(--body-background-color);
                padding: 0;
                @include padding-start(8%);  
            }       
        }
        &.line-solid h2:before {
            border-style: solid;
        }  
        &.line-dotted h2:before {
            border-style: dotted;
        } 
        &.line-dashed h2:before {
            border-style: dashed;
        } 
    }
    &.line-solid .separator-sub-line {
        border-style: solid;
    }
    &.line-dotted .separator-sub-line {
        border-style: dotted;
    }  
    &.line-dashed .separator-sub-line {
        border-style: dashed;
    }  
}
