        text-align: center;
        padding: 15px 0 0;   
        .title {      
            margin: 0 auto 6%;      
            font-family: var(--font-heading); 
            line-height: 1.0;    
            span {
                font-weight: 300;
            }
        }
        .subtitle {            
            font-family: var(--font-heading);
            margin: 0 auto 0;
            line-height: 1;      
            @media (min-width: 480px) {
                margin-bottom: 10px;
            }
    
        }
        .caption {           
            font-size: var(--body-font-size);
            line-height: 22px;
            font-family: var(--font-heading);      
        }    
        .btn {     
            padding: 7px 30px;
            line-height: 26px;
            font-size: var(--body-font-size);
            font-weight: var(--font-bold);
            text-transform: uppercase;   
            border: none;  
            @include transform(translateZ(0));
            @media (max-width: 479px) {
                padding-top: 4px;
                padding-bottom: 4px;
                font-size: calc(var(--body-font-size) - 2px);
                display: none;
            }
            &:hover {
                @include opacity(0.8);
            } 
        }
        .is-countdown .countdown-section { 
            padding: 0px 4px;
            display: inline-block;
            position: relative;
            float: none;
            @media (min-width: 480px) {
                padding: 0 7px;
            }  
            @include is-countdown-separate(25px, 25px, 25px);
            .countdown-amount {
                background: transparent;
                font-weight: 700;
                background: var(--bg-deal-section);
                color: var(--text-deal-text);  
                line-height: 25px;
                padding: 3px 7px;  
                @include border-radius(5px, 5px, 5px, 5px);
            }
            .countdown-period { 
                font-size: 12px;
                text-transform: uppercase;
                float: none;              
                padding: 5px 0 0;
                color: var(--body-title-color);
            }
        } 
    } 
    &.text-box-overlay {
        .text {
            @include ab(n, n, n, n);      
            padding: 5%;   
            z-index: 1;         
            .offer {
                white-space: nowrap;
                @media (max-width: 1199px) and (min-width: 992px) {
                    @include scale(0.9, 0.9);
                }
                .text {
                    position: static;
                    display: block;
                    text-transform: uppercase;
                    padding: 0 0 15px;
                    @include translate(0, 0);
                }
            }
        }
        &.top-left .text {
            text-align: start;
            top: 0;
            left: 0;        
        }
        &.top-center .text {
            top: 0;
            left: 50%;
            display: table;
            @include translate(-50%, 0);
        }
        &.top-right .text {
            text-align: end;
            top: 0;
            right: 0;        
        }
        &.center-left .text {
            top: 50%;
            left: 0;
            text-align: start;
            @include translate(0, -50%);
        }
        &.center-center .text {
            top: 50%;
            left: 50%;
            display: table;
            @include translate(-50%, -50%);
        }
        &.center-right .text {
            top: 50%;
            right: 0;
            text-align: end;
            @include translate(0, -50%);
        }
        &.bottom-left .text {
            text-align: start;
            bottom: 0;
            left: 0;        
        }
        &.bottom-center .text {
            bottom: 0;
            left: 50%;
            display: table;
            @include translate(-50%, 0);
        }
        &.bottom-right .text {
            text-align: end;
            bottom: 0;
            right: 0;        
        } 
    }     
}
