                position: static;
            }
            .owl-carousel .owl-nav {
                top: -42px;
            } 
        }
    } 
    .deal-position-2 {
        @include clearfix();
        @include flexbox();
        flex-wrap: wrap;
        margin: 0 -15px;  
        .product-type-item {
            padding: 0 15px;
            overflow: hidden;
            width: 100%;
            max-width: 100%;
            @include flex(0 0 100%);
            @media (min-width: 768px) {
                max-width: 33.3333333333%;
                @include flex(0 0 33.3333333333%);
            }
            @media (min-width: 992px) {
                max-width: 25%;
                @include flex(0 0 25%);
            }
            @media (min-width: 1200px) {
                max-width: 19.1666666667%; 
                @include flex(0 0 19.1666666667%);
            }
            .countdown-title {
                font-size: calc(var(--size-h2) - 9px);
