            } 
        } 
        .product-wrapper {
            border: 2px solid var(--main-color); 
            @include border-radius-20;
            &:before,
            &:after {
                content: none;
            }  
        }
    }  
}
.special-product-label {    
    @include ab(0, n, n, 0);    
    z-index: 1;
    background: var(--body-label-sale-background);
    color: var(--body-label-sale-color);
    padding: 19px 5px;
    line-height: 1.2;
    text-align: center;
    width: 76px;
    height: 76px;
    @include border-radius-circle;
    .text {
        display: block;
        font-size: 12px;
    }  
    .percent {
        display: block;
        font-size: var(--size-h4);
        font-weight: 700;
    }  
}
/*=== Customize Block ===*/
.custom-content-width {
    padding: 0 15px;
    .col-md-12 {    
        padding: 0;
        margin: 0 auto;   
    }
}
.custom-content-wrapper {
    &.custom-content-margin-top {
        margin-top: 30px;    
    }
    &.custom-content-margin-bottom {
        margin-bottom: 30px    
    }
    &.only-min-height .custom-item {
        &.col-md-12 {
            position: relative;
            @media (max-width: 767px) {
                min-height: 10px !important;
            }
            .custom-item-inner {  
                width: 100%;  
                padding: 0;         
                @media (min-width: 768px) {
                    height: 100%;
                    width: 100%; 
                    @include ab(50%, 50%, n, n);   
                    @include translate(-50%, -50%);
                }          
                &.custom-item-inner-text {
                    .custom-block-heading-text {
                        margin: 0 auto;
                    }
                    > div {
                        @media (min-width: 768px) {
                            padding-top: 90px;
                        }
                    }
                }
            }
            &.align-center .custom-item-inner {
                text-align: center;
            } 
            &.align-left .custom-item-inner {
                text-align: start;
            } 
            &.align-right .custom-item-inner {
                text-align: end;
            }
        }
    }
}
.custom-content-header {
    margin: 0 auto 3%;
}
.custom-item-inner-collection,
.custom-item-inner-html {
    display: block;
}
.custom-block-heading-text,
.custom-block-body-text {  
    @include margin-start(auto); 
    @include margin-end(auto);  
}
.custom-block-heading-text {
    font-weight: 700;
}
.custom-block-body-text p {
    font-size: var(--body-font-size);
    margin: 0;
} 
.custom-content { 
    width: auto;    
    padding: 0; 
    margin: -7.5px;  
    @include flexbox();
    -ms-flex-align: stretch;
    -webkit-align-items: stretch;
    -moz-align-items: stretch;
    -ms-align-items: stretch;
    -o-align-items: stretch;
    align-items: stretch;
    -webkit-flex-wrap: wrap;
    -moz-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    .custom-item  {
        padding: 7.5px;
        @include flex(0 0 auto);  
        .custom-block-image-gallery .row {
            margin: -7.5px;     
            > div {
                padding: 7.5px;  
            }
        }
    }
    .placeholder-svg {    
        fill: rgba(0, 0, 0, 0.35);
        width: 100%;
        height: 100%;
        max-width: 100%;
        max-height: 100%;
        background: #f7f7f7;
    }
    .info {   
        padding: 15px 20px;
    } 
    .custom-block-heading-text {
        margin: 0 auto 3%;
    }
}
.custom-item-inner {
    position: relative;  
    max-width: 100%;
    &.custom-item-inner-bgimage {
        max-width: 1170px;
        margin: 0 auto;
        padding: 0 4%;    
        .custom-block-heading-text {
            max-width: 100%;
        }    
    } 
}
.custom-item-inner-product,
.custom-item-inner-collection {
    background: #ebebeb;
    .info {
        > a {
            @extend .h5;
            letter-spacing: 0;
            text-transform: none;
            font-family: var(--font-heading);
            @media (min-width: 480px) {    
                text-transform: uppercase;
                letter-spacing: 2px;
            }
        }
        .product-author {
            color: var(--body-light-color);
            font-size: var(--body-font-size);      
            padding: 0 0 10px;
        }
        .product-name {
            color: var(--body-title-product);             
            margin: 0 0 5px;                
            text-transform: none; 
            letter-spacing: 0;      
        }
        .product-price {
            display: inline-block;
        }
    }
}
.image-layer {
    @include ab(50%, 0, n, 0);
    z-index: 2;
    max-height: 100%;
    max-width: 100%;
    margin: 0 auto;
    padding: 0 15px;
    @include translate(0, -50%);   
    @media (min-width: 768px) {
        width: 760px;
    }
    @media (min-width: 992px) {
        width: 980px;
    }
    @media (min-width: 1200px) {
        width: 1200px;
    }
    img {
        max-width: 69%;
        max-height: 100%;
        display: inline-block;  
        @media (min-width: 480px) {
            max-width: 49%;
        }
        @media (min-width: 768px) {
            max-width: 60%;
        }
        @media (min-width: 1025px) {
            max-width: 64%;
        }
        @media (min-width: 1200px) {
            max-width: 100%;
        }
    }
    &.layer-center {    
        text-align: center;      
    }
    &.layer-left {
        text-align: start;
    }
    &.layer-right {
        text-align: end;
    } 
}  
/*=== Menu & Image Section ===*/
.menu-image-section {
    .menu-image-content {
        border: 1px solid var(--body-border-color); 
        justify-content: space-between;
        flex-wrap: wrap; 
        @include flexbox;  
        @include border-radius-10;  
    } 
    .box-menu {
        padding: 22px 15px;  
        background: #f9f9f9; 
        max-width: 50%; 
        @include flex(0 0 50%); 
        @include border-radius(10px, 0px, 0px, 0px);
        @media (min-width: 576px) {
            @include padding-start(24px);
            @include padding-end(24px); 
        } 
        @media (min-width: 992px) {
            max-width: 20%; 
            @include flex(0 0 20%);
            @include border-radius(10px, 0px, 0px, 10px);
        }
        li {  
            list-style: none;  
            a {
                font-size: calc(var(--body-font-size) - 1px);  
                color: var(--body-link-color);
            } 
            &:hover > a {
                color: var(--main-color);
            }
        }
        .heading {
            font-weight: 700; 
            padding-bottom: 9px;
            display: block; 
        }
        .d-menu {
            @include padding-start(10px);
            li {
                padding-bottom: 7px; 
            }   
        } 
    }
    .nav-tab-wrapper {
        margin: 0;
        background: #fefffe;
        max-width: 20%;   
        @include flex(0 0 20%);  
        @media (max-width: 991px) {
            max-width: 50%;   
            @include flex(0 0 50%); 
            @include border-radius(0px, 10px, 0px, 0px);
        }  
        .nav-tabs {
            padding: 19px 15px;
            border-bottom: none; 
            @media (min-width: 576px) {
                @include padding-start(20px);
                @include padding-end(20px);
            }  
            &:before {
                content: none;
            }
            li {
                display: block; 
                padding-bottom: 12px;
                margin-bottom: 10px;  
                border-bottom: 1px solid var(--body-border-color); 
                &:last-child {
                    border-bottom: none; 
                }
                a {
                    text-align: start;
                    font-size: calc(var(--body-font-size) - 1px); 
                    padding-bottom: 0;
                    color: var(--body-link-color);  
                    &:before, &:after {
                        content: none;
                    }
                    &.active {
                        font-weight: 400;
                    } 
                    &:hover {
                        color: var(--main-color); 
                    }
                }
            }
        }
    }
    .tab-content {
        max-width: 100%;  
        @include flex(0 0 100%); 
        @include border-start(none); 
        @media (min-width: 992px) {
            max-width: 60%;  
            @include flex(0 0 60%);
            @include border-start(1px solid var(--body-border-color)); 
        } 
        img {
            @include border-radius(0px, 0px, 10px, 10px);
            @media (min-width: 992px) {
                @include border-radius(0px, 10px, 10px, 0px);
            }  
        }
    } 
}
/*=== Section popular search ===*/
.popular-search-wrapper {
    ul { 
        flex-wrap: wrap;
        @include flexbox;
        > li {  
            list-style: none;
            background: #f5f5f5; 
            padding: 14px 30px; 
            margin-top: 8px;  
            @include margin-end(15px);  
            @include border-radius(5px, 5px, 5px, 5px);
            &:last-child {
                @include margin-end(0);    
            } 
            a {
                color: var(--body-link-color);
                &:hover {
                    color: var(--main-color); 
                }
            }
        }
    }
}